*{
  box-sizing: border-box;
}

body{
    background-color: #f14647;
    margin: 0;

}

h2{
  background-color: #f14647;
  padding: 10px;
  color:#ffffff;
}
.App{
  padding: 30px;
  background-color: #fff;
  width: 80%;
  margin: 50px auto;
  box-shadow: 1px 2px 5px #eee;
}

input{
  width: 80%;
  border: 0;
  padding: 6px 10px;
  background-color: #e8e8e8;
  outline: none;
}

button{
  width: 20%;
  border: 0;
  padding:6px 0;
  outline:0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  background-color: #4dc55b;
}

ul{
  list-style: none;
  padding: 0;
}

li{
  margin: 15px 0;
}

li span{
  display: inline-block;
  width: 60%;
  color: #6a6a6a;
}

li > button:first-of-type{
  background-color: #6a6a6a;
  color: #fff;
  margin-right: 1%;
  width: 19%;
}

li > button:last-of-type{
  background-color: #d9534f;
  color: #fff;
}

ul >form{
  margin: 15px 0;
}

.text-one{
    font-size: x-large;
    text-align: center;
}